'use strict';

class DataProcessing {
	static #deviceType = 'BUZMEDIC'; //데이터 파싱 타입
	static isReverse = true; //데이터 역전여부
	static shiftNumFirst = 16;
	static shiftNumSecond = 8;
	static shiftNumThird = 6;
	static hex = 0x7ffff;

	static #TransData(data, bits) {
		return data & (1 << (bits - 1)) ? data - (1 << bits) : data;
	}

	static ParseECG(bytes) {
		return Array.from(bytes).flatMap((data, idx, array) => {
			if (!idx || idx % 3 !== 2) return [];
			const shiftByte = ((array[idx - 2] << this.shiftNumFirst) | (array[idx - 1] << this.shiftNumSecond) | (array[idx] << this.shiftNumThird)) >> 6;
			const TransDataResult = this.#TransData(shiftByte, 18);
			return this.#deviceType.includes('BUZMEDIC') ? TransDataResult * (this.isReverse ? -1 : 1) : TransDataResult;
		});
	}

	static ParsePPG(bytes) {
		return Array.from(bytes).flatMap((data, idx, array) => {
			if (idx || idx % 3 !== 2) return [];
			return (((array[idx - 2] << this.shiftNumFirst) | (array[idx - 1] << this.shiftNumSecond) | array[idx]) & this.hex) * (this.isReverse ? -1 : 1);
		});
	}

	static BynaryClassify(data) {
		const [startCode, dataType, dataLength, ...etc] = new Uint8Array(data.target.value.buffer);
		const dataBody = str.slice(3, dataLength * 3 + 3);
		return {
			startCode,
			dataType,
			dataBody,
		};
	}

	static setDeviceType(type) {
		this.#deviceType = type;
	}

	static getDeviceType() {
		return this.#deviceType;
	}
}

export { DataProcessing };
