'use strict';

import Notiflix from 'notiflix';
Notiflix.Loading.init({
	messageFontSize: '3vh',
	svgSize: '90',
	svgColor: '#0db5e7',
	messageColor: '#0db5e7',
	messageMaxLength: 60,
});

class BlueToothInterface {
	#instance;
	#CHARACTERISTICS;
	#deivceOptions;
	#characterObj;
	constructor(pUuid = '6e400001-b5a3-f393-e0a9-e50e24dcca9e', charObj) {
		this.#instance;
		this._primaryUuid = pUuid;
		//버즈 메딕용 필터명
		this.buzMedicFilterName = 'BUZMEDIC_';
		//버즈 큐브용 필터명
		this.buzCubeFilterName = 'BUZCUBE_';
		this.#deivceOptions = {
			filters: [{ namePrefix: null }],
			optionalServices: [this._primaryUuid],
		};
		this.#characterObj = {
			tx: charObj ?? '6e400003-b5a3-f393-e0a9-e50e24dcca9e',
			rx: charObj ?? '6e400002-b5a3-f393-e0a9-e50e24dcca9e',
		};
	}
	get instance() {
		return this.#instance;
	}
	set instance(connection) {
		this.#instance = connection;
	}
	get characterObj() {
		return this.#characterObj;
	}
	get deivceOptions() {
		return this.#deivceOptions;
	}
	get deviceFilterOpt() {
		const { namePrefix } = this.#deivceOptions['filters'][0];
		if (!namePrefix) {
			this.#deivceOptions['filters'][0].namePrefix = this.buzMedicFilterName;
		}
		return namePrefix;
	}
	set deviceFilterOpt(filterNm) {
		this.#deivceOptions['filters'][0].namePrefix = filterNm;
	}
	get primaryUuid() {
		return this._primaryUuid;
	}
	set primaryUuid(uuid) {
		this._primaryUuid = uuid;
	}
	get CHARACTERISTICS() {
		return this.#CHARACTERISTICS;
	}
	set CHARACTERISTICS(data) {
		this.#CHARACTERISTICS = data;
	}
}
/**
 * 블루투스 Class
 */
class BlueTooth extends BlueToothInterface {
	constructor() {
		super();
	}
	/**
	 * BlueTooth 연결
	 * @returns BlueTooth
	 */
	async ReqBlueToothDevice() {
		if (this.instance) {
			this.instance.gatt.disconnect();
			this.CHARACTERISTICS = null;
		}
		this.instance = await navigator.bluetooth.requestDevice(this.deivceOptions).catch(e => console.error(e));
		if (!this.instance) {
			Notiflix.Notify.info('BlueTooth 요청에 실패');
			return;
		}
		//this.instance.addEventListener('gattserverdisconnected', () => Notiflix.Notify.warning('BlueTooth 연결이 해제되었습니다.'));
		const { connected } = this.instance.gatt;
		if (connected) {
			console.log('================= [ ConnectTry ] GATT Disconnect ================ ');
			this.instance.gatt.disconnect();
			this.CHARACTERISTICS = null;
		}

		Notiflix.Loading.dots('BlueTooth연결 중입니다. 잠시만 기다려 주십시오.');

		await this.instance.gatt
			.connect()
			.then(result => {
				console.log(`===== GATT connect Success =====`);
				return result;
			})
			.then(primaryService => primaryService.getPrimaryService(this.primaryUuid))
			.then(async gattService => {
				console.log(`===== PrimaryService Success =====`);
				Notiflix.Notify.success('PrimaryService Success');
				return await gattService.getCharacteristics();
			})
			.then(characteristic => {
				Notiflix.Loading.remove();
				Notiflix.Notify.success('Characteristic Success');
				this.CHARACTERISTICS = characteristic;
			})
			.catch(e => {
				console.error(e);
				Notiflix.Loading.remove();
				Notiflix.Notify.info('BlueTooth GATT 연결 중 실패하였습니다.');
			});
		return this;
	}
	/**
	 * 특정 CHARACTERISTIC Return
	 * @param {String} type BlueTooth객체의  CHARACTERISTICS명
	 * @returns CHARACTERISTIC
	 */
	GetTypeCharacterInst(type) {
		return this.CHARACTERISTICS.find(one => one.uuid === this.characterObj[type]);
	}
}

export { BlueTooth };
